'use client';

import { useState, useEffect, useCallback } from 'react';
import Link from 'next/link';
import { useRouter } from 'next/navigation';

const API_BASE_URL = 'http://localhost:3001';

// --- Type Definitions ---
declare global { interface Window { ethereum?: any; }}

interface Room {
  id: string;
  name: string;
  ticketPrice: string;
}

interface HistoryItem {
    roundNumber: number;
    roomName: string;
    ticketPrice: string;
    wasWinner: boolean;
}

// --- MyPage Modal ---
const MyPageModal = ({ account, onClose }: { account: string, onClose: () => void }) => {
    const [activeMainTab, setActiveMainTab] = useState('history'); // 'history' or 'wallet'
    const [allHistory, setAllHistory] = useState<HistoryItem[]>([]);
    const [rooms, setRooms] = useState<Room[]>([]);
    const [activeRoomId, setActiveRoomId] = useState<string>('');
    const [isLoading, setIsLoading] = useState(true);
    const [error, setError] = useState<string | null>(null);

    useEffect(() => {
        const fetchHistoryAndRooms = async () => {
            setIsLoading(true);
            setError(null);
            try {
                const [historyRes, roomsRes] = await Promise.all([
                    fetch(`${API_BASE_URL}/user-history/${account}`),
                    fetch(`${API_BASE_URL}/rooms`)
                ]);

                if (!historyRes.ok) throw new Error('Failed to fetch history.');
                if (!roomsRes.ok) throw new Error('Failed to fetch rooms.');

                const historyData = await historyRes.json();
                const roomsData = await roomsRes.json();
                
                setAllHistory(historyData.history || []);
                setRooms(roomsData || []);
                if (roomsData.length > 0 && !activeRoomId) {
                    setActiveRoomId(roomsData[0].id);
                }

            } catch (err: any) {
                setError(err.message);
            } finally {
                setIsLoading(false);
            }
        };
        fetchHistoryAndRooms();
    }, [account, activeRoomId]);

    const filteredHistory = allHistory.filter(item => item.roomName.includes(rooms.find(r => r.id === activeRoomId)?.name || ''));

    return (
        <div className="fixed inset-0 bg-black bg-opacity-70 flex justify-center items-center z-50" onClick={onClose}>
            <div className="bg-gray-800 rounded-lg w-full max-w-2xl text-left shadow-xl flex flex-col" onClick={(e) => e.stopPropagation()}>
                {/* Header and Main Tabs */}
                <div className="flex justify-between items-center p-4 border-b border-gray-700">
                    <div className="flex gap-6">
                        <button onClick={() => setActiveMainTab('history')} className={`text-xl font-semibold pb-2 ${activeMainTab === 'history' ? 'text-white border-b-2 border-white' : 'text-gray-500 hover:text-white'}`}>
                            History
                        </button>
                        <button onClick={() => setActiveMainTab('wallet')} className={`text-xl font-semibold pb-2 ${activeMainTab === 'wallet' ? 'text-white border-b-2 border-white' : 'text-gray-500 hover:text-white'}`}>
                            Wallet
                        </button>
                    </div>
                    <button onClick={onClose} className="text-gray-400 hover:text-white text-3xl">&times;</button>
                </div>

                {/* Content Area */}
                <div className="p-6">
                    {activeMainTab === 'history' && (
                        <div>
                            <div className="flex border-b border-gray-600 mb-4">
                                {rooms.map(room => (
                                    <button
                                        key={room.id}
                                        onClick={() => setActiveRoomId(room.id)}
                                        className={`py-2 px-4 font-semibold transition-colors duration-200 ${activeRoomId === room.id ? 'border-b-2 border-yellow-400 text-yellow-400' : 'text-gray-400 hover:text-white'}`}
                                    >
                                        {room.name}
                                    </button>
                                ))}
                            </div>
                            {isLoading ? <div className="w-8 h-8 border-4 border-yellow-400 border-t-transparent rounded-full animate-spin mx-auto my-4"></div>
                            : error ? <p className="text-red-500 text-center">{error}</p>
                            : filteredHistory.length > 0 ? (
                                <ul className="space-y-3 max-h-80 overflow-y-auto pr-2">
                                    {filteredHistory.map((item, index) => (
                                        <li key={index} className={`p-4 rounded-md flex justify-between items-center ${item.wasWinner ? 'bg-yellow-500 text-black' : 'bg-gray-700'}`}>
                                            <div className="flex items-center gap-4">
                                                <span className="bg-black text-white text-center font-bold rounded-md px-3 py-2">Round #{item.roundNumber}</span>
                                                <div>
                                                    <p className="font-semibold">Winning # <span className="text-lg">{item.roundNumber}</span></p> {/* Placeholder */}
                                                </div>
                                                <div>
                                                    <p className="font-semibold">My # <span className="text-lg">{item.roundNumber}</span></p> {/* Placeholder */}
                                                </div>
                                            </div>
                                            <span className="text-2xl font-bold">{item.wasWinner ? 'WIN' : 'LOSE'}</span>
                                        </li>
                                    ))}
                                </ul>
                            ) : <p className="text-gray-500 text-center py-4">No participation history for this room.</p>}
                        </div>
                    )}
                    {activeMainTab === 'wallet' && (
                        <div>
                            <h3 className="text-xl font-bold mb-4">Connected Wallet</h3>
                            <div className="bg-gray-700 p-4 rounded-lg">
                                <p className="text-gray-400">Address:</p>
                                <p className="text-white font-mono break-all">{account}</p>
                            </div>
                        </div>
                    )}
                </div>
            </div>
        </div>
    );
};


// --- Main Lobby Page Component ---
export default function LobbyPage() {
  const [rooms, setRooms] = useState<Room[]>([]);
  const [error, setError] = useState<string | null>(null);
  const [account, setAccount] = useState<string | null>(null);
  const [isMyPageVisible, setIsMyPageVisible] = useState(false);
  const router = useRouter();

  const connectWallet = async () => {
    if (typeof window.ethereum !== 'undefined') {
        try {
            const accounts = await window.ethereum.request({ method: 'eth_requestAccounts' });
            setAccount(accounts[0]);
        } catch (err) { setError('Failed to connect wallet.'); }
    } else { setError('Please install MetaMask.'); }
  };
  
  useEffect(() => {
    const handleAccountsChanged = (accounts: string[]) => setAccount(accounts.length > 0 ? accounts[0] : null);
    if (window.ethereum) {
        window.ethereum.on('accountsChanged', handleAccountsChanged);
        (async () => {
            const accounts = await window.ethereum.request({ method: 'eth_accounts' });
            handleAccountsChanged(accounts);
        })();
        return () => window.ethereum.removeListener('accountsChanged', handleAccountsChanged);
    }
  }, []);

  useEffect(() => {
    const fetchRooms = async () => {
      try {
        const response = await fetch(`${API_BASE_URL}/rooms`);
        if (!response.ok) {
          throw new Error('Failed to fetch rooms from the server.');
        }
        const data: Room[] = await response.json();
        setRooms(data);
      } catch (err: any) {
        setError(err.message);
      }
    };
    fetchRooms();
  }, []);

  const handleEnterRoom = (roomId: string) => {
    router.push(`/room/${roomId}`);
  };

  return (
    <main className="flex min-h-screen flex-col items-center justify-center bg-gray-900 text-white p-6 relative">
       <div className="absolute top-6 right-6">
            {account ? (
                <div className="flex items-center gap-4">
                    <span className="text-sm bg-green-900 text-green-300 rounded px-3 py-2">{`${account.substring(0, 6)}...${account.slice(-4)}`}</span>
                    <button onClick={() => setIsMyPageVisible(true)} className="bg-blue-600 text-white font-bold py-2 px-4 rounded hover:bg-blue-500">
                        My Page
                    </button>
                </div>
            ) : (
                <button onClick={connectWallet} className="bg-blue-600 text-white font-bold py-2 px-4 rounded hover:bg-blue-500">
                    Connect Wallet
                </button>
            )}
        </div>

      <div className="text-center">
        <h1 className="text-6xl font-extrabold mb-4 text-yellow-400">Crypto Lotto</h1>
        <p className="text-xl text-gray-300 mb-12">Choose a room to test your luck!</p>
        
        {error && <p className="text-red-500 mb-4">{error}</p>}

        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-8">
          {rooms.length > 0 ? rooms.map((room) => (
            <button
              key={room.id}
              onClick={() => handleEnterRoom(room.id)}
              className="group bg-gray-800 border-2 border-gray-700 rounded-lg p-8 transform transition-all duration-300 hover:scale-105 hover:border-yellow-400 hover:shadow-2xl hover:shadow-yellow-500/20"
            >
              <h2 className="text-3xl font-bold mb-2 text-white group-hover:text-yellow-400">{room.name}</h2>
              <p className="text-lg text-gray-400">
                Ticket: <span className="font-semibold text-xl text-white">{room.ticketPrice}</span> USDT
              </p>
            </button>
          )) : (
            <p>Loading rooms...</p>
          )}
        </div>
      </div>
      {isMyPageVisible && account && <MyPageModal account={account} onClose={() => setIsMyPageVisible(false)} />}
    </main>
  );
}
